import React, { useLayoutEffect, useRef, useState } from 'react';
import Day from './day';
import Today from './todaysWthr';
import C from '../animated/celsius.svg?sanitize=true'
import F from '../animated/fahrenheit.svg?sanitize=true'

const DaysList = React.memo(props => {

    const [fehr, setfehr] = useState(true)
    const ref = useRef()

    useLayoutEffect(() => {
        ref.current.scrollIntoView({ behavior: "smooth", block: 'start' });

    }, [])


    let days = [];
    let obj = [];
    let date = props.daysWeather[0].dt_txt.slice(8, 11)
    for (let i = 0; i < props.daysWeather.length; i++) {

        if (props.daysWeather[i].dt_txt.slice(8, 11) === date) {
            obj.push(props.daysWeather[i])
        }
        else {
            days.push(obj)
            obj = []
            date = props.daysWeather[i].dt_txt.slice(8, 11)
            obj.push(props.daysWeather[i])
        }

    }
    days.push(obj)




    days.map(day => (
        day.map(d => {
            if (fehr) {
                d.main.temp = Math.round((d.main.temp - 32) * 5 / 9)
                d.main.temp_max = Math.round((d.main.temp_max - 32) * 5 / 9)
                d.main.temp_min = Math.round((d.main.temp_min - 32) * 5 / 9)
            }
            else {
                d.main.temp = Math.round((d.main.temp * 9 / 5 + 32))
                d.main.temp_max = Math.round((d.main.temp_max * 9 / 5 + 32))
                d.main.temp_min = Math.round((d.main.temp_min * 9 / 5 + 32))
            }

            return (d)
        })
    ))

    return (
        <>
            <div className='states'>
                <div className='header' ref={ref}>
                    <div>
                        <h1>{props.country.name}</h1>
                        <h3>{days[0][0].dt_txt.slice(5, 10)}</h3>
                    </div>

                    <div>
                        <button onClick={() => (setfehr(!fehr))}><img src={fehr ? C : F} className='subSvg' /></button>
                    </div>
                </div>
                <Today today={days[0]} country={props.country} />
                {days.slice(1).map(day => (<Day details={day} key={day[0].dt} />))}
            </div>

        </>
    )

})
export default DaysList;